body {
  font-family: "PT Sans", "Arial", sans-serif;
  margin: 0;
  padding: 0;

  @media (min-width: @desktop-width) {
    background-color: @lightest-gray;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
